<template>
  <div>
    <b-card
      ref="cardAction"
      title=" "
    >
      <show-data
        label=" "
        :first-name="husband.firstName"
        :middle-name="husband.firstName"
        :last-name="husband.firstName"
        :email="husband.firstName"
        :birthdate="husband.firstName"
        :birthplace="husband.firstName"
        :marital="husband.firstName"
        :nationality="husband.nationality"
        :profession="husband.profession"
        :address="husband.address"
        :phone="husband.phone"
      />
      <show-data
        label=" "
        :first-name="wife.firstName"
        :middle-name="wife.firstName"
        :last-name="wife.firstName"
        :email="wife.firstName"
        :birthdate="wife.firstName"
        :birthplace="wife.firstName"
        :marital="wife.firstName"
        :nationality="wife.nationality"
        :profession="wife.profession"
        :address="wife.address"
        :phone="wife.phone"
      />
      <show-data
        label="  "
        :first-name="firstWitness.firstName"
        :middle-name="firstWitness.firstName"
        :last-name="firstWitness.firstName"
        :email="firstWitness.firstName"
        :birthdate="firstWitness.firstName"
        :birthplace="firstWitness.firstName"
        :marital="firstWitness.firstName"
        :nationality="firstWitness.nationality"
        :profession="firstWitness.profession"
        :address="firstWitness.address"
        :phone="firstWitness.phone"
      />
      <show-data
        label="  "
        :first-name="secondWitness.firstName"
        :middle-name="secondWitness.firstName"
        :last-name="secondWitness.firstName"
        :email="secondWitness.firstName"
        :birthdate="secondWitness.firstName"
        :birthplace="secondWitness.firstName"
        :marital="secondWitness.firstName"
        :nationality="secondWitness.nationality"
        :profession="secondWitness.profession"
        :address="secondWitness.address"
        :phone="secondWitness.phone"
      />
      <show-data
        label=" "
        :first-name="agent.firstName"
        :middle-name="agent.firstName"
        :last-name="agent.firstName"
        :email="agent.firstName"
        :birthdate="agent.firstName"
        :birthplace="agent.firstName"
        :marital="agent.firstName"
        :nationality="agent.nationality"
        :profession="agent.profession"
        :address="agent.address"
        :phone="agent.phone"
        :kinship-degree="agent.kinshipDegree"
      />

      <hr>

      <show-images
        :husband-front="husbandFront"
        :husband-back="husbandBack"
        :wife-front="wifeFront"
        :wife-back="wifeFront"
        :first-witness-front="firstWitnessFront"
        :first-witness-back="firstWitnessBack"
        :second-witness-front="secondWitnessFront"
        :second-witness-back="secondWitnessBack"
        :agent-front="agentFront"
        :agent-back="agentBack"
        :divorce-certificate="divorceCertificate"
        :husband-death-certificate="husbandDeathCertificate"
      />

    </b-card>
  </div>
</template>

<script>
import { localize } from 'vee-validate'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import '@core/scss/vue/libs/vue-wizard.scss'
import { required } from '@validations'
import ShowData from '@/views/admin/contracts/ShowData.vue'
import ShowImages from '@/views/admin/contracts/ShowImages.vue'
import {
  // BRow,
  // BCol,
  // BFormSelect,
  BCard,
} from 'bootstrap-vue'
// import ar from 'vee-validate/dist/locale/ar.json'

// Install English and Arabic locales.
// localize({
//   dic,
// })
const dic = {
  ar: {
    names: {
      email: ' ',
      phone: ' ',
      Password: ' ',
      'Password Confirm': '  ',
      website: ' ',
      country: '',
      landline_number: ' ',
      direct_employee_name: '  ',
      direct_employee_phone: '   ',
      factory_register: ' ',
      factory_register_image: '  ',
      letter_image: ' ',
      address: '',
    },
  },
}
localize('ar', dic.ar)
/* eslint guard-for-in: 0 */
/* eslint no-restricted-syntax: 0 */
export default {
  name: 'ViewContract',
  components: {
    // BRow,
    // BCol,
    BCard,
    ShowData,
    ShowImages,
  },
  data() {
    return {
      quranDate: null,
      isMosque: 1,
      quranAddress: null,
      selected: 'first',
      options: [
        { text: ' ', value: 1 },
        { text: ' ', value: 0 },
      ],
      array: [],
      husbandFront: null,
      husbandBack: null,
      wifeFront: null,
      wifeBack: null,
      firstWitnessFront: null,
      firstWitnessBack: null,
      secondWitnessFront: null,
      secondWitnessBack: null,
      agentFront: null,
      agentBack: null,
      divorceCertificate: null,
      husbandDeathCertificate: null,
      type: null,
      status: null,
      husband: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '2020-01-01',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'husband',
      },
      wife: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'wife',
      },
      firstWitness: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'first_witness',
      },
      secondWitness: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        type: 'second_witness',
      },
      agent: {
        firstName: '',
        middleName: '',
        lastName: '',
        email: '',
        birthdate: '22-22-2035',
        birthplace: '',
        maritalStatus: 'single',
        nationality: '',
        profession: '',
        address: '',
        phone: '',
        kinshipDegree: '',
        type: 'agent',
      },
      required,
      // ? Default locale for VeeValidate is 'en'
      locale: 'ar',
    }
  },
  created() {
    this.$store.dispatch('contract/show', this.$route.params.id).then(res => {
      this.fetchData(res.data.data)
    })
  },
  methods: {
    prepareData() {
      this.array = []
      this.array.push(this.husband)
      this.array.push(this.wife)
      this.array.push(this.firstWitness)
      this.array.push(this.secondWitness)
      this.array.push(this.agent)
    },
    formSubmitted() {
      this.prepareData()
      const formData = new FormData()
      formData.append('quran_date', this.quranDate)
      formData.append('is_mosque', this.isMosque)
      formData.append('quran_address', this.quranAddress)

      // Object.keys(this.contract).forEach(key => formData.append(key, this.contract[key]))
      this.array.forEach((value, index) => {
        formData.append(`data[${index}][first_name]`, value.firstName)
        formData.append(`data[${index}][middle_name]`, value.middleName)
        formData.append(`data[${index}][last_name]`, value.lastName)
        formData.append(`data[${index}][email]`, value.email)
        formData.append(`data[${index}][birth_date]`, value.birthdate)
        formData.append(`data[${index}][birth_place]`, value.birthplace)
        formData.append(`data[${index}][marital_status]`, value.maritalStatus)
        formData.append(`data[${index}][nationality]`, value.nationality)
        formData.append(`data[${index}][profession]`, value.profession)
        formData.append(`data[${index}][address]`, value.address)
        formData.append(`data[${index}][phone_number]`, value.phone)
        formData.append(`data[${index}][type]`, value.type)
        formData.append(`data[${index}][kinship_degree]`, value.kinshipDegree)
      })
      this.$store.dispatch('contract/store', formData).then(() => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: '   ',
            icon: 'EditIcon',
            variant: 'success',
          },
        })
      })
    },
    formatDate(datetime) {
      const array = datetime.split(' ')
      return array[0]
    },
    fetchData(data) {
      if (data) {
        this.dataId = data.id
        // eslint-disable-next-line camelcase
        this.isMosque = data.is_mosque
        // eslint-disable-next-line camelcase
        this.dataDiscount = data.created_at
        // eslint-disable-next-line camelcase
        this.quranAddress = data.quran_address
        // eslint-disable-next-line camelcase
        this.quranDate = this.formatDate(data.quran_date)
        // this.dataCode = status
        // eslint-disable-next-line camelcase
        this.dataCode = data.user_id

        this.husbandFront = data.husbandFront
        this.husbandBack = data.husbandBack
        this.wifeFront = data.wifeFront
        this.wifeBack = data.wifeBack
        this.firstWitnessFront = data.firstWitnessFront
        this.firstWitnessBack = data.firstWitnessBack
        this.secondWitnessFront = data.secondWitnessFront
        this.secondWitnessBack = data.secondWitnessBack
        this.agentFront = data.agentFront
        this.agentBack = data.agentBack
        this.divorceCertificate = data.divorceCertificate
        this.husbandDeathCertificate = data.husbandDeathCertificate

        data.person.forEach(person => {
          if (person.type === 'husband') {
            // alert(person.first_name)
            this.husband.firstName = person.first_name
            this.husband.middleName = person.middle_name
            this.husband.lastName = person.last_name
            this.husband.email = person.email
            this.husband.birthdate = person.birth_date
            this.husband.birthplace = person.birth_place
            this.husband.maritalStatus = person.marital_status
            this.husband.nationality = person.nationality
            this.husband.profession = person.first_name
            this.husband.address = person.address
            this.husband.phone = person.phone_number
          }
          if (person.type === 'wife') {
            this.wife.firstName = person.first_name
            this.wife.middleName = person.middle_name
            this.wife.lastName = person.last_name
            this.wife.email = person.email
            this.wife.birthdate = person.birth_date
            this.wife.birthplace = person.birth_place
            this.wife.maritalStatus = person.marital_status
            this.wife.nationality = person.nationality
            this.wife.profession = person.first_name
            this.wife.address = person.address
            this.wife.phone = person.phone_number
          }
          if (person.type === 'first_witness') {
            this.firstWitness.firstName = person.first_name
            this.firstWitness.middleName = person.middle_name
            this.firstWitness.lastName = person.last_name
            this.firstWitness.email = person.email
            this.firstWitness.birthdate = person.birth_date
            this.firstWitness.birthplace = person.birth_place
            this.firstWitness.maritalStatus = person.marital_status
            this.firstWitness.nationality = person.nationality
            this.firstWitness.profession = person.first_name
            this.firstWitness.address = person.address
            this.firstWitness.phone = person.phone_number
          }
          if (person.type === 'second_witness') {
            this.secondWitness.firstName = person.first_name
            this.secondWitness.middleName = person.middle_name
            this.secondWitness.lastName = person.last_name
            this.secondWitness.email = person.email
            this.secondWitness.birthdate = person.birth_date
            this.secondWitness.birthplace = person.birth_place
            this.secondWitness.maritalStatus = person.marital_status
            this.secondWitness.nationality = person.nationality
            this.secondWitness.profession = person.first_name
            this.secondWitness.address = person.address
            this.secondWitness.phone = person.phone_number
          }
          if (person.type === 'agent') {
            this.agent.firstName = person.first_name
            this.agent.middleName = person.middle_name
            this.agent.lastName = person.last_name
            this.agent.email = person.email
            this.agent.birthdate = person.birth_date
            this.agent.birthplace = person.birth_place
            this.agent.maritalStatus = person.marital_status
            this.agent.nationality = person.nationality
            this.agent.profession = person.first_name
            this.agent.address = person.address
            this.agent.phone = person.phone_number
            this.agent.kinshipDegree = person.kinship_degree
          }
        })
      }
    },
    validationFormDate() {
      return new Promise((resolve, reject) => {
        this.$refs.infoDateRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationForm() {
      return new Promise((resolve, reject) => {
        this.$refs.husbandRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormWife() {
      return new Promise((resolve, reject) => {
        this.$refs.wifeRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormFirstWitness() {
      return new Promise((resolve, reject) => {
        this.$refs.firstWitnessRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormSecondWitness() {
      return new Promise((resolve, reject) => {
        this.$refs.secondWitnessRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    validationFormAgent() {
      return new Promise((resolve, reject) => {
        this.$refs.agentRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
  },
}
</script>
<style scoped></style>
